var that = this;
export default {
	data() {
		return {
			order_refund: {},
			express_index: '',
			express_list: [],
			modalName:''
		};
	},
	methods: {
		hidden(){
			that.modalName=''
		},
		showModel(e){
			that.modalName='kuaidi'
		},
		prinCat(e){
			that.express_index=e.detail.value
		},
		getinfo() {
			this.$request
				.request({
					url: that.api.order.refund_detail,
					data: {
						order_refund_id: that.id
					}
				})
				.then(res => {
					console.log(res);
					that.order_refund = res;
					that.express_list = res.express_list;
				});
		},
		copyinfo: function(e) {
			uni.setClipboardData({
				data: e.currentTarget.dataset.info,
				success: function(t) {
					uni.showToast({
						title: '复制成功！',
						icon: 'success',
						mask: 'true'
					});
				}
			});
		},
		sendddd(e){
			let value=e.detail.value;
			let req=true,title='';
			value.express?'':(req=false,title='请选择快递公司');
			value.express_no?'':(req=false,title='请填写快递单号');
			if(!req){
				that.$showModal(title);
				return
			} 
			this.$request
				.request({
					url: that.api.order.refund_send,
					data: {
						order_refund_id: that.id,
						express:value.express,
						express_no:value.express_no,
						orderType: 'STORE'
					},
					method:'post'
				})
				.then(res => {
					console.log(res); 
					that.hidden()
					that.$alertok("提交成功！")
					that.getinfo();
			});
		}
	},
	onLoad(option) {
		that = this;
		that.id = option.id ? option.id : '1';
		that.getinfo();
	}
};
